import Connection from '../../controller/Connection'
import i18n from '../../controller/i18n'

export default 'Alias Devices'
export const namespace = 'alias'

export const deviceObjectType = 'channel';


export function parse(deviceStructure, options) {
	const socket = Connection.getConnection;
	
	return new Promise(resolve => {
		
		socket.getObject(deviceStructure.root).then(obj => {
			
			let device = {
				'name': typeof obj.common.name === 'object' ? i18n.t(obj.common.name) : obj.common.name,
				'function': 'other',
				'states': {}
			}
				
			// convert alias structure to jarvis
			let states = {}
			let role = obj.common.role;
			
			if (['light', 'rgbSingle', 'ct', 'hue', 'dimmer'].indexOf(role) > -1) { // convert light // 'rgb' not supported
				device.function = 'light';
				states = {
					'power': '.ON',
					'level': [ '.DIMMER', '.SET' ],
					'colorTemperature': '.TEMPERATURE',
					'hue': '.HUE',
					'rgb': '.RGB',
				}
			}
			else if (['blind'].indexOf(role) > -1) { // convert blind
				device.function = 'blind';
				states = {
					'level': '.SET',
					//'': '.ACTUAL',
					//'': '.STOP',
				}
			}
			
			Object.keys(states).forEach(stateKey => {
				
				states[stateKey] = Array.isArray(states[stateKey]) ? states[stateKey] : [states[stateKey]];
				states[stateKey].forEach(key => {
					
					if (deviceStructure.states.indexOf(deviceStructure.root + key) > -1) {
						device.states[stateKey] = deviceStructure.root + key;
					}
					
					//delete states[stateKey]
				});
			});
			
			// adopt all other states
			let statesAsArray = Object.values(device.states);
			deviceStructure.states.forEach(state => {
				
				if (statesAsArray.indexOf(state) === -1) {
					device.states = {
						...device.states,
						[state.substring(state.lastIndexOf('.')+1)]: state
					}
				}
			});
			
			// resolve
			resolve(device);
		});
	});
}